* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* effect*/
.mouse {
  position: absolute;
  background-color: orange;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  transform: translateX(-50%) translateY(-50%);
  transition: 1s;
  mix-blend-mode: difference;
}
body {
  background-color: rgb(240, 240, 240);
  font-family: Poppins;
  width: 100vw;
  height: 100vh;
  scroll-snap-stop: 20px;
}

img {
  width: 80%;
}

.contenedorTexto {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 20px;
  font-weight: 900;
}

h1 {
  font-size: 180px;
  font-weight: 900;
  -webkit-text-stroke: black;
  -webkit-text-stroke-width: 5px;
  -webkit-text-fill-color: transparent;
  text-shadow: 0px 10px 40px rgb(240, 240, 240);
}

h4 {
  font-size: 35px;
  font-weight: 300;
}

/*estilos de imaganes y texto para lazy loading*/
section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 100vh;
  align-items: center;
  justify-items: center;
}

.contenedorImg {
  width: 100%;
  height: 100%;
  padding: 60px 0px 0px 20px;
}

.contenedorImgR {
  width: 100%;
  height: 100%;
  padding: 60px 20px 0px 0px;
  text-align: right;
}

.contenedorImg img {
  width: 80%;
}

section p {
  font-size: 70px;
  font-weight: 500;
  width: 80%;
  text-align: center;
}
